nav {
    background: linear-gradient(to right, rgba(15, 76, 129, 0.8), rgba(50, 140, 193, 0.8));
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: flex-end;
    height: 5.5rem;
    width: 100%;
    z-index: 999;
  }

.links {
    display: flex;
    text-decoration: none;
    list-style-type: none;
    font-size: 1.5rem;
    height: 3.1rem;
    
}

#name-nav {
    position: absolute;
    left: 0;
    margin-left: 2rem;
}

ul, li {
    padding-left: 0.5rem;
    margin-left: 2.5rem;
    margin-top: .85rem;
}

nav > ul > li:hover {
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    transition: background-color 0.3s ease-out;
  }

#contact-nav {
    padding-right: 0.5rem;
    margin-right: 1.2rem;
}

i {
    margin-right: .5rem;
}
